<template lang="pug">
  .add-contact
    .add-header
      button(@click='goBack').add-header__option Отменить
      h1.add-header__title Контакт
      button(@click='create').add-header__option Готово

    .add-user
      img(src='../../assets/img/icons/user.svg').add-user__icon
      .add-user__fields
        input(
          type='text' placeholder='Имя' 
          v-model.trim='contact.name'
          :class="{'add-contact__input_error': $v.contact.name.$error}").add-contact__input
        input(
          type='text' placeholder='Фамилия' 
          v-model.trim='contact.surname'
          :class="{'add-contact__input_error': $v.contact.surname.$error}").add-contact__input

    .add-phone
      p.add-phone__icon
      input(
        type='number' 
        placeholder='Номер' 
        v-model.trim='contact.phone'
        :class="{'add-contact__input_error': $v.contact.phone.$error}").add-contact__input
</template>

<script>
  import {required, maxLength, minLength} from 'vuelidate/lib/validators'
  import {mapState} from 'vuex'

  export default {
    props: ['options',],
    data() {
      return {
        contact: {
          name: '',
          surname: '',
          phone: ''
        },
      }
    },
    validations: {
      contact: {
        name: {required, minLength: minLength(3), maxLength: maxLength(10)},
        surname: {required, minLength: minLength(3), maxLength: maxLength(12)},
        phone: {required, minLength: minLength(6), maxLength: maxLength(11)},
      },
    },
    computed: mapState(['calls', 'player']),
    methods: {
      goBack() {
        this.$router.go(-1)
      },
      create() {
        if (this.contact.phone == this.player.phoneNumber)
          return this.$alert.show('Введен собственный номер')

        let findContact = this.calls.contacts
          .find(item => item.number == this.contact.phone);

        if (findContact && this.options.type == 'create')
          return this.$alert.show('Данный номер уже добавлен');

        this.$v.contact.$touch();

        if (this.$v.contact.$error) 
          return this.$alert.show('Поля заполнены некорректно')

        if (this.options.type == 'change') {
          this.$store.commit('CHANGE_USER_IN_CONTACTS', {prev: this.options.data, edited: this.contact});
          this.$alert.show('Контакт изменен');
          this.$router.go(-2)
        }
        else {
          this.$store.commit('ADD_USER_TO_CONTACTS', this.contact);
          this.$alert.show('Контакт добавлен');
          this.goBack()
        }
      }
    },
    created() {
      let options = this.options.data;
      
      if (this.options.type == 'change') {
        let [name,surname] = options.name.split(' ');

        this.contact.name    = name
        this.contact.surname = surname
        
      }
      this.contact.phone  = options.number
    }
  }
</script>

<style lang="sass">

  @import '../../assets/sass/phone/apps/calls/create'

</style>